  color: #fff;
  font-family: Roboto;
  font-size: 64px;
  font-weight: 500;
  line-height: 75px;
}

@keyframes rotateHeroSvg {
  0% {
    transform: rotate(0);
  }

  50% {
    transform: rotate(90deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.svg-hero {
  animation: 4s linear infinite rotateHeroSvg;
}

.switch {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #ccc;
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.slider:before {
  content: "";
  background-color: #fff;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  transition: all .4s;
  position: absolute;
  bottom: 4px;
  left: 3px;
}

input:checked + .slider {
  background-color: #0abf49;
}

input:checked + .slider:before {
  transform: translateX(16px);
}

body.dark-mode {
  color: #fff;
  background-color: #292929;
}

.hero-section.dark-mode {
  background-color: #333;
}

.slider.dark-mode {
  background-color: #757575;
}

